@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

*{
  font-family:'Ubuntu', sans-serif;
}

#mobHomePgImg{
    display: none;
}

.inputBox{
    margin:10px;
    color: hsl(213, 96%, 18%);
    font-weight: bold;
}

.inputBox-box{
    border: 2px solid hsl(229, 24%, 87%);
    border-radius: 5px;
    width: 450px;
    height: 50px;
    padding: 0px;
    margin-bottom: 20px;
}

.headComponent{
    font-weight:bolder;
    padding-left:10px;
}
.heading{
    color: hsl(213, 96%, 18%);
    font-size: 2rem;
    margin-bottom: 10px;
}
.subHeading{
    color:hsl(231, 11%, 63%);
}

.mainContainer{
    display: flex;
    justify-content: center;
}

.container2{
    padding: 50px 100px 0px 100px;
    width:620px;
}
.container1{
    display: flex;
    justify-content: space-between;
    margin-top:100px;
    padding: 20px;
    border-radius: 10px;
    /* background-color: rgba(241, 246, 250, 0.945);  */
    /* box-shadow: 0px 0px 20px rgba(180, 189, 196, 0.733);     */
} 

.mainFormContainer{
    height:500px
}

.homePgImg{
height: 700px;
/* box-shadow: 5px 10px black; */

}


.formContainer{
    display: flex;
    justify-content: left;

}
.btnContainer{
    text-align: right;
    padding-right:10px;
}

.formContent-content{
    margin-bottom: 50px;
}


#sideNavList{
    position: absolute;
    top: 120px;
    padding:60px;

}

/* .sideNavbarList{
    display: flex;
    justify-content: center;
} */

.sideNavListContainer{
    display: flex;
    align-items: center;
    color: white;
    font-weight: bolder;
    margin-bottom: 30px;
    position: relative;
    top: 10px;
    transition: ease-in-out top .1s;
}

.sideNavListContainer:hover{
    position: relative;
    font-size: 18px;
    top: 3px;
}

.navListItemHead{
    font-size: 12px;
    color:hsl(229, 24%, 87%);
}

.navItemNo{
    border: solid 2px white;
    border-radius: 50%;
    padding: 7px 10px 7px 10px;
/* background-color: hsl(213, 96%, 18%); */
}
.navItemNumber{
    border-radius: 50%;
    margin-right: 20px;
}

.planContainer{
    border: solid 1px hsl(229, 24%, 85%);
    padding: 15px;
    margin: 10px;
    width: 150px;
    height: 200px;
    border-radius: 10px;
}

.planContainer:hover{
    border:solid 2px hsl(243, 100%, 62%);
    background-color: hsla(229, 100%, 84%, 0.267);
}

.planDetailsContainer{
    display: flex;
    flex-wrap: wrap;    
}

.planContainer2{
    position: relative;
    top: 80px;
}

.step3PlanContainer{
    display: grid;
    grid-template-columns:40px 280px 160px;
    align-items: center;
    border: solid 1px black;
    width:fit-content;
    margin-bottom: 20px;
    border-radius: 5px;
}

.step3Plans{
    padding: 20px;
}

#step3PlanPrice{
    text-align: right;
}

.btnContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.btns{
    width: 150px;
    height: 50px;
    background-color: hsl(213, 96%, 18%);
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 10px;
    border: none;
    position: relative;
    top: 10px;
    transition: ease-in-out top 0.1s
}

.btns:hover{
    top: 5px;  
}

.formBtn{
    display: flex;
    justify-content: right;
    align-items: center;
}

#btn2{
    color:hsl(231, 11%, 60%);
    font-size: 1.1rem;
    font-weight: 600;
    background-color: white;
    border: none;
}

#btn2:hover{
    color:hsl(213, 96%, 18%) ;
}



.checkBoxInput{
    transform: scale(1.3);
}

.addOnsHead{
    color: hsl(213, 96%, 18%) ;
    font-weight: bolder;
    margin-bottom: 5px;
}

.addOnsPrice{
    color: hsl(243, 100%, 60%);
}

.step3PlanContainer:hover{
    border:solid 1.2px hsl(243, 100%, 62%);
    background-color: hsla(229, 100%, 84%, 0.178);
}


.summary{
    /* display: flex;
    justify-content: space-between; */

    margin: 0 50px 0 50px;
}

.summaryContainer1{
    background-color: hsla(229, 100%, 84%, 0.267);
    padding: 20px;
    border-radius: 10px;
}

.summaryContainer2{
    padding: 20px;
}

.billSummary{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.formInput:invalid{
    display: block;
}

.selectedPlan{
    color: hsl(213, 96%, 18%) ;
    font-weight: bolder;
    font-size: 1.2rem;
}

.selectedAddOns{
    color:hsl(231, 11%, 60%);
}

.totalBill{
    font-size: 1.2rem;
    color:  hsl(243, 100%, 62%);
    font-weight: bolder;
}

