@media screen and (max-width:1440px) {
    

    #btn3{
        background-color:hsl(243, 100%, 60%);
    }
    
    #btn3:hover{
        background-color: hsl(243, 100%, 80%);
    }
    
}

@media screen and (max-width: 992px) {

    /* *{
        font-size:16px
    } */

    .navItemList{
        display: none;
    }

    .imgContainer{
        max-width: 800px;
        width: 100%;
    }

    #mobHomePgImg{
        display: block;
        /* width: 800px;
        max-width: 100%;
        height: auto; */
    
    }
    
    #deskHomePgImg{
        display: none;
    }

    .navItemList{
        display: none;
    }

    #sideNavList{
        display: flex;
        position: none;
        top: 50px;
        padding:0px;
    
    }

    .sideNavbarList{
        display: flex;
        justify-content: center;
    }

    /* .demo1{
        display: flex;
        justify-content: center;
    } */
    .mainFormContainer{
        
        padding: 30px 10px 25px 10px;
        margin: 30px 10px 0px 10px ;
        width: 335px;
        height:450px;
        position: absolute;
        top: 100px;
        background-color: white;
        border-radius: 10px;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.13);
    }

    .sideNavListContainer{
        display: flex;
        align-items: center;
        color: white;
        font-weight: bolder;
        margin-bottom: 30px;
        position: relative;
        top: 10px;
        transition: ease-in-out top .1s;
    }

    .container1{
        display: block;
        margin-top:0px;
        padding: 0px; 

    }

    .container2{
        padding:0px;
        width:100px;
    }

    .inputBox-box{
        width: 275px;
        height: 30px;
    }

    .btnContainer{
        width: 260px;
        align-items: center;
        position: fixed;
        bottom: 30px;
        padding: 5px;
    }

    .btns{
        width: 100px;
        height: 35px;
        font-size: 16px;
        /* font-weight: bold; */
        border-radius: 5px;
    }  

    .planContainer{
        display: flex;
        align-items: center;
        padding: 15px;
        /* margin: 10px; */
        width: 265px;
        height: auto;
    }

    .planContainer2{
        margin-left:20px ;
        position: unset;
        top: unset;
    }

    .step3PlanContainer{
        grid-template-columns:30px 200px 75px;
        width: 330px;
        margin-bottom: 10px;
        border-radius: 5px;
    }

    .step3Plans{
        padding: 15px;
    }

    .summary{
        margin: 0px;
    }

    #btn3{
        background-color:hsl(243, 100%, 60%);
    }
    
    #btn3:hover{
        background-color: hsl(243, 100%, 60%);
    } 

}





